import * as React from 'react';
import styled, { createGlobalStyle } from 'styled-components';
import { FormattedMessage, useIntl } from 'react-intl';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faExclamationTriangle, faInfoCircle } from '@fortawesome/free-solid-svg-icons';
import { IAppState } from 'ts/background/store';
import {
    disableAutoScroll,
    enableAutoScroll,
    hideOrganicPosts,
    trackPageView,
    setRated,
    showOrganicPosts,
} from 'ts/background/store/settings';
import { FormGroup, Typography, Switch, FormControlLabel, Button } from '@mui/material';
import { faHeart } from '@fortawesome/free-regular-svg-icons';
import { useDispatch, useSelector } from 'react-redux';

import { log } from '../../scripts/logger';

const GlobalStyle = createGlobalStyle`
	body {
		box-sizing: border-box;
		display: block;
		margin: 0;
		overflow: hidden;
	}`;

const PopupAppContainer = styled('div')`
    width: 385px;
    > div {
        display: flex;
        border-bottom: 2px solid #f9930f;
        padding: 15px;
    }
`;

const PopupAppHeader = () => (
    <StyledPopupAppHeader>
        <img height="55" src="/assets/logo.png" />
    </StyledPopupAppHeader>
);

const StyledPopupAppHeader = styled.div`
    align-items: center;
    justify-content: center;
    height: 60px;
    background: #0fbcf9;

    > h1 {
        color: #fff;
        margin-right: 5px;
    }
`;

const RateUs = () => {
    const dispatch = useDispatch();
    const handleClick = () => {
        const userAgent = (
            navigator.userAgent.match(
                /(opera|chrome|safari|firefox|msie)\/?\s*(\.?\d+(\.\d+)*)/i,
            )?.[1] || ''
        ).toLowerCase();
        if (userAgent === 'firefox') {
            window.open('https://addons.mozilla.org/en-US/firefox/addon/my-ad-finder', '_blank');
        } else {
            window.open(
                'https://chromewebstore.google.com/detail/my-ad-finder/jdelodjlpgkjenhcongcfdcocmjgjbci?utm_source=facebook&utm_campaign=social',
                '_blank',
            );
        }
        dispatch(setRated());
    };
    return (
        <StyledRateUs>
            <a onClick={handleClick}>
                <img height="80" src="/assets/rate-us.png" />
            </a>
        </StyledRateUs>
    );
};

const StyledRateUs = styled.div`
    align-items: center;
    justify-content: center;
    > a {
        cursor: pointer;
    }
`;

interface IMetricsProps {
    liked: number;
    found: number;
}

const Metrics = ({ liked, found }: IMetricsProps) => (
    <StyledMetrics>
        <Typography color="textPrimary">
            <FormattedMessage id="app.popup.metrics" defaultMessage="Metrics" />
        </Typography>

        <div>
            <div>
                <Typography color="textPrimary">
                    <FormattedMessage id="app.popup.liked" defaultMessage="Liked" />
                </Typography>
                <Typography color="secondary">{liked}</Typography>
            </div>

            <div>
                <Typography color="textPrimary">
                    <FormattedMessage id="app.popup.found" defaultMessage="Found" />
                </Typography>

                <Typography color="secondary">{found}</Typography>
            </div>
        </div>
    </StyledMetrics>
);

const StyledMetrics = styled.div`
    text-align: center;
    flex-direction: column;
    font-size: 18px;

    > div {
        display: flex;
        justify-content: space-between;
        > div {
            display: flex;
            flex-direction: column;
        }
    }
`;

interface FooterProps {
    collectedAdsMessage: string;
    collectedAds: number;
}

const Footer: React.FunctionComponent<FooterProps> = ({
    collectedAds,
    collectedAdsMessage,
}: FooterProps) => {
    const openOptionPage = () => {
        try {
            chrome.runtime.openOptionsPage();
            window.close();
        } catch {
            window.open(`chrome-extension://${chrome.runtime.id}/options.html`, '_blank');
        }
    };

    return (
        <StyledFooter>
            {collectedAds >= 0 && (
                <Button
                    variant="contained"
                    color="primary"
                    href="https://app.myadfinder.com"
                    target="_blank"
                >
                    <FormattedMessage id="app.popup.ad_spy" defaultMessage="Ad Spy" />
                </Button>
            )}
            <Button variant="outlined" color="primary" onClick={openOptionPage}>
                {collectedAdsMessage}
            </Button>
        </StyledFooter>
    );
};

const StyledFooter = styled.div`
    align-items: center;
    justify-content: space-around;
`;

const FacebookRedirect = () => (
    <StyledFacebookRedirect>
        <Typography color="textPrimary" component="p" variant="body1" align="center">
            <FormattedMessage
                id="app.popup.fb_redirect"
                defaultMessage="Go to <a>facebook.com</a> to find more products"
                values={{
                    a: (chunks: any) => (
                        <a target="_blank" href="https://facebook.com">
                            {chunks}
                        </a>
                    ),
                }}
            />
        </Typography>
    </StyledFacebookRedirect>
);

const StyledFacebookRedirect = styled.div`
    align-items: center;
    justify-content: center;
`;

interface ContactSupportProps {
    collectedAdsMessage: string;
}

const ContactSupport: React.FunctionComponent<ContactSupportProps> = ({
    collectedAdsMessage,
}: ContactSupportProps) => {
    return (
        <StyledContactSupport>
            <ContactSupportWidgets>
                <FontAwesomeIcon icon={faHeart} size="3x" color="#f9930f" />
                <FreshchatWidget src="/assets/freshchat.png" />
            </ContactSupportWidgets>

            <Typography color="textPrimary" component="p" variant="body2" align="center">
                <FormattedMessage
                    id="app.popup.contact_support"
                    defaultMessage="{icon} Please contact us from {collectedAds} page if the heart button doesn't appear on your feed sponsored posts."
                    values={{
                        a: (chunks: any) => (
                            <a
                                target="_blank"
                                href={`chrome-extension://${chrome.runtime.id}/options.html`}
                            >
                                {chunks}
                            </a>
                        ),
                        collectedAds: collectedAdsMessage,
                        icon: <FontAwesomeIcon icon={faInfoCircle} />,
                    }}
                />
            </Typography>
        </StyledContactSupport>
    );
};

const StyledContactSupport = styled.div`
    align-items: center;
    justify-content: center;
    > p {
        line-height: 1.5;
    }
`;

const ContactSupportWidgets = styled.div`
    display: flex;
    flex-direction: column;
`;

const FreshchatWidget = styled.img`
    height: 50px;
    margin-left: -5px;
`;

const Controls = ({ disabled }: { disabled: boolean }) => {
    const dispatch = useDispatch();
    const organicPosts = useSelector((state: IAppState) => state.settings.organicPosts);
    const autoScroll = useSelector((state: IAppState) => state.settings.autoScroll);
    const { formatMessage } = useIntl();
    const showOnlyAdsHandler = () => {
        if (organicPosts) {
            dispatch(hideOrganicPosts());
        } else {
            dispatch(showOrganicPosts());
        }
    };
    const autoScrollHandler = () => {
        if (autoScroll) {
            dispatch(disableAutoScroll());
        } else {
            dispatch(enableAutoScroll());
        }
    };
    return (
        <StyledOnlySponsored>
            {disabled && (
                <Typography variant="subtitle2" color="secondary" align="center">
                    <FormattedMessage
                        id="app.popup.user_scroll"
                        defaultMessage="{icon} Please find the first ad manually before you can use these options. Please make sure to use an old Facebook account."
                        values={{
                            icon: <FontAwesomeIcon icon={faExclamationTriangle} />,
                        }}
                    />
                </Typography>
            )}
            <FormGroup row={true}>
                <FormControlLabel
                    control={
                        <Switch
                            color="primary"
                            checked={!organicPosts}
                            disabled={disabled}
                            onChange={showOnlyAdsHandler}
                        />
                    }
                    label={
                        <Typography color="textPrimary">
                            {formatMessage({
                                id: 'app.popup.only_ads_switch',
                                defaultMessage: 'Show only ads',
                            })}
                        </Typography>
                    }
                    labelPlacement="start"
                />

                <FormControlLabel
                    control={
                        <Switch
                            color="primary"
                            checked={autoScroll}
                            disabled={disabled}
                            onChange={autoScrollHandler}
                        />
                    }
                    label={
                        <Typography color="textPrimary">
                            {formatMessage({
                                id: 'app.popup.auto_scroll',
                                defaultMessage: 'Auto scroll',
                            })}
                        </Typography>
                    }
                    labelPlacement="start"
                />
            </FormGroup>
        </StyledOnlySponsored>
    );
};

const StyledOnlySponsored = styled.div`
    flex-direction: column;
`;

const PopupApp = () => {
    const dispatch = useDispatch();
    const { collectedAds, data } = useSelector((state: IAppState) => state.ads);
    const rated = useSelector((state: IAppState) => state.settings.rated);
    const { formatMessage } = useIntl();
    const [isFacebook, setIsFacebook] = React.useState(false);
    const collectedAdsMessage = formatMessage({
        id: 'app.popup.collected_ads',
        defaultMessage: 'Collected ads',
    });

    React.useEffect(() => {
        log("PopupApp.tsx: trackPageView('popup')");
        dispatch(trackPageView('popup'));
    }, []);

    React.useEffect(() => {
        chrome.tabs.query({ active: true, lastFocusedWindow: true }, (tabs) => {
            const activeTab = tabs[0];
            setIsFacebook(!!activeTab?.url && activeTab.url.indexOf('facebook.com') > -1);
        });
    }, []);

    return (
        <>
            <GlobalStyle />
            <PopupAppContainer>
                <PopupAppHeader />
                {isFacebook ? (
                    <>
                        <Controls disabled={collectedAds === 0} />
                        {collectedAds === 0 && (
                            <ContactSupport collectedAdsMessage={collectedAdsMessage} />
                        )}
                    </>
                ) : (
                    <FacebookRedirect />
                )}
                <Metrics
                    liked={data.filter(({ favorite }) => !!favorite).length}
                    found={collectedAds}
                />
                <Footer collectedAds={collectedAds} collectedAdsMessage={collectedAdsMessage} />
                {!rated && collectedAds > 100 && <RateUs />}
            </PopupAppContainer>
        </>
    );
};

export default PopupApp;
