import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { IntlProvider as IntlProvider_, ReactIntlErrorCode } from 'react-intl';
import { IAppState } from 'ts/background/store';
import { setLocale } from 'ts/background/store/settings';
import { log } from 'ts/scripts/logger';
import isEmpty from 'lodash/isEmpty';

const onError = (e: any) => {
    if (e.code === ReactIntlErrorCode.MISSING_DATA) {
        return;
    }
    log(e, 'onError');
};

const IntlProvider: React.FC<Omit<React.ComponentProps<typeof IntlProvider_>, 'locale'>> = (
    props,
) => {
    const dispatch = useDispatch();
    const locale = useSelector((state: IAppState) => state.settings.locale);
    const messages = useSelector((state: IAppState) => state.settings.messages);

    useEffect(() => {
        if (isEmpty(messages)) {
            dispatch(setLocale(locale));
        }
    }, [messages, locale, dispatch]);

    return !isEmpty(messages) ? (
        <IntlProvider_
            onError={onError}
            defaultLocale="en"
            {...props}
            locale={locale}
            messages={messages}
        />
    ) : null;
};

export default IntlProvider;
